import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import "../Lobby.css";

const Lobby = ({ username }) => {
  const [rooms, setRooms] = useState([]);
  const [newRoomName, setNewRoomName] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem("accessToken");
    if (!token) {
      alert(" .");
      navigate("/");
      return;
    }

    fetch("/api/rooms", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then(res => res.ok ? res.json() : Promise.reject("   "))
      .then(data => setRooms(data))
      .catch(err => setError(err.toString()));
  }, [navigate]);

  const handleCreateRoom = async (e) => {
    e.preventDefault();
    const trimmedRoomName = newRoomName.trim();
    if (!trimmedRoomName) return setError("  .");
    if (trimmedRoomName.length < 2 || trimmedRoomName.length > 50)
      return setError("2~50 ");

    const token = localStorage.getItem("accessToken");
    try {
      const res = await fetch("/api/rooms", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({ name: trimmedRoomName }),
      });
      if (!res.ok) throw new Error("  ");
      const newRoom = await res.json();
      setRooms([...rooms, newRoom]);
      setNewRoomName("");
      setError("");
    } catch (err) {
      console.error(err);
      setError(err.message);
    }
  };

  const handleJoinRoom = (roomId) => {
    const username = localStorage.getItem("username");
    if (!username) {
      alert(" .  .");
      navigate("/");
      return;
    }
    navigate(`/chat/${roomId}`, { state: { username } });
  };

  return (
    <div className="lobby-container">
      <h2> </h2>
      <p>, {username}!</p>
      <div className="room-list">
        {rooms.length === 0 ? (
          <p> .</p>
        ) : (
          <ul>
            {rooms.map((room) => (
              <li key={room.id}>
                {room.name}{" "}
                <button onClick={() => handleJoinRoom(room.id)}></button>
              </li>
            ))}
          </ul>
        )}
      </div>
      <form onSubmit={handleCreateRoom}>
        <input
          type="text"
          value={newRoomName}
          onChange={(e) => setNewRoomName(e.target.value)}
          placeholder="  "
        />
        <button type="submit"> </button>
      </form>
      {error && <p className="error-message">{error}</p>}
    </div>
  );
};

export default Lobby;
