import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import api from '../api'; // axios  (JWT   )
import { toast } from 'react-toastify';
import '../Login.css';

const NicknameSetup = ({ onLogin }) => {
  const [nickname, setNickname] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();
 
const handleSubmit = async (e) => {
  e.preventDefault();
   const trimmed = nickname.trim();

    if (!trimmed) return setError(' .');
    if (trimmed.length < 2 || trimmed.length > 20) return setError(' 2~20  .');

  const token = localStorage.getItem("accessToken");

  try {
    const response = await fetch("/api/user/set-nickname", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({ nickname }),
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.message || "  ");
    }
    localStorage.setItem("username", nickname)
    
    toast.success("  !");
    navigate("/lobby");
  } catch (err) {
    toast.error(err.message || "  .");
  }
};

  return (
    <div className="login-container">
      <h2> </h2>
      <form onSubmit={handleSubmit} className="login-form">
        <input
          id="nickname"
          type="text"
          value={nickname}
          onChange={(e) => setNickname(e.target.value)}
          placeholder=" "
          className={error ? 'input-error' : ''}
          autoFocus
        />
        {error && <p className="error-message">{error}</p>}
        <button type="submit"></button>
      </form>
    </div>
  );
};

export default NicknameSetup;
