import React, { useState } from "react";
import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import LoginUnified from "./components/LoginUnified";
import Signup from "./components/Signup";
import NicknameSetup from "./components/NicknameSetup";
import Lobby from "./components/Lobby";
import Chat from "./components/Chat";
import ProtectedRoute from "./components/ProtectedRoute";
import { ToastContainer } from "react-toastify";

function App() {
  const [username, setUsername] = useState(() => localStorage.getItem("username"));

  const handleLogout = () => {
    localStorage.clear();
    setUsername(null);
    window.location.href = "/";
  };

  return (
    <Router>
      <ToastContainer position="top-center" autoClose={2000} />
      {username && <button onClick={handleLogout}></button>}
      <Routes>
        <Route path="/" element={<LoginUnified onLogin={() => setUsername(null)} />} />
        <Route path="/signup" element={<Signup />} />
        <Route path="/nickname" element={username ? <Navigate to="/lobby" /> : <NicknameSetup setUsername={setUsername} />} />
        <Route path="/lobby" element={<Lobby username={username} />} />
        <Route path="/chat/:roomId" element={<Chat />} />
        {/* <Route path="*" element={<Navigate to="/" />} /> */}
      </Routes>
    </Router>
  );
}

export default App;
